@import '../../../index.scss';

.sidenav {
  height: 100%;
  width: 17%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-image: $PrimaryBgLinear;
  border-radius: 0px 17px 17px 0px;
  overflow-x: hidden;

  .logo-wrapper {
    border-bottom: 1px solid $PrimaryTextColor;

    .logo {
      width: 120px;
      height: 65px;
      align-items: center;
      margin: auto;
      margin-top: 20px;
      margin-bottom: 20px;

      img {
        width: 100%;
        height: 100%;
      }
    }
  }

  .profile {
    position: absolute;
    width: 100%;
    height: 130px;
    bottom: 0;
    color: #fff;
    cursor: pointer;

    &::before {
      opacity: 0.1;
      background: #000;
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
    }

    .user-logout,
    .user-details {
      height: 60px;
      padding: 10px 32px;
      display: flex;
      align-items: center;
      border-top: 1px solid $OpacityReducer;

      .menu-list {
        padding-left: 25px;
      }

      .logout-icon {
        width: 20px;
        height: 20px;
      }
    }

    .user-logout {
      .menu-icon {
        padding-left: 10px;
        padding-top: 5px;
      }
    }

    .user-details {
      height: 70px;

      .menu-list {
        padding-left: 12px;
      }

      .menu-icon {
        width: 40px;
        height: 40px;

        .user-logo {
          width: inherit;
          height: inherit;
        }
      }
    }
  }
}

.sidenav a {
  padding: 15px 0px 15px 18.5px;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  display: flex;
  outline: none;
}

.sidenav a:hover {
  color: #f1f1f1;
  text-decoration: none;
}

.sidenavul {
  list-style-type: none;
  margin-bottom: 0px;
  padding-left: 24px;
  margin-top: 25px;

  [data-key='Hub'] {
    a {
      .active-circle {
        background: #ed1d2e;
      }
    }
  }
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}

.main {
  margin-left: auto;
  font-size: 14px;
  margin-left: 17%;
  padding: 10px;
}

.nav-active {
  color: $textColor !important;
  background-color: #f3f4fb;
  border-radius: 32px 0px 0px 32px;
  position: relative;
  $navWidth: 24px;
  $navHeight: 24px;

  .active-area-top,
  .active-area-bottom {
    width: $navWidth;
    height: $navHeight;
    background-color: #f3f4fb;
    position: absolute;
    right: 0px;
    overflow: hidden;
    z-index: -1;

    .active-circle {
      position: absolute;

      width: $navWidth * 2;
      height: $navHeight * 2;
      border-radius: 50%;
      background: #f3183e;
    }
  }

  .active-area-top {
    top: -($navHeight);

    .active-circle {
      top: -($navHeight);
      left: -($navWidth);
    }
  }

  .active-area-bottom {
    bottom: -($navHeight);

    .active-circle {
      bottom: -$navHeight;
      left: -($navWidth);
    }
  }
}

.menu-list {
  display: inline-block;
  padding-left: 15px;
  align-self: center;
}

.menu-icon {
  display: inline-block;
  width: 26px;
}
